    async createOrder(email, myCart, t, details) {
        // gen receipt code
        let x = await firebase.database().ref("care_orders").once('value');
        let valid = false;
        let receipt = "";
        while (valid === false) {
            valid = true;
            let result = [];
            let characters = 'ABCDEFGHJKLMNPQRSTUVWXYZ23456789';
            let charactersLength = characters.length;
            for (let i = 0; i < 7; i++ ) {
                result.push(characters.charAt(Math.floor(Math.random() * charactersLength)));
            }
            receipt = result.join('');
            for (let k in x.val()) {
                if (x.val()[k].receipt) {
                    if (receipt === CryptoJS.AES.decrypt(x.val()[k].receipt, "receiptEncrypted").toString(CryptoJS.enc.Utf8)) {
                        valid = false;
                    }
                }
            }
        }
        
        // get totals, items and user
        // let token = email;
        // let tokenEncrypted = CryptoJS.AES.encrypt(token, "tokenEncrypted").toString();
        // console.log("1) " + token);        
        // console.log("2) " + tokenEncrypted); // save this to db
        // console.log("3) " + CryptoJS.AES.decrypt(tokenEncrypted, "tokenEncrypted").toString());
        // console.log("4) " + CryptoJS.AES.decrypt(tokenEncrypted, "tokenEncrypted").toString(CryptoJS.enc.Utf8)); // decrypted
        let d = new Date();
        let dt = d.toISOString().toString().substring(0, 10);
        await firebase.database().ref("care_orders").push({
            user : CryptoJS.AES.encrypt(email, "emailEncrypted").toString(),
            items : CryptoJS.AES.encrypt(myCart, "mycartEncrypted").toString(),
            receipt : CryptoJS.AES.encrypt(receipt, "receiptEncrypted").toString(),
            total : CryptoJS.AES.encrypt(t.toString(), "totalEncrypted").toString(),
            details : CryptoJS.AES.encrypt(details, "detailsEncrypted").toString(),
            dateordered: CryptoJS.AES.encrypt(dt, "dtEncrypted").toString(),
            confirmed : 0
        });
        console.log("order has been added to database");
        return receipt;
    }

