    async getOrderById(receipt) {
        let order = {};
        let x = await firebase.database().ref("care_orders").once('value');
        for (let k in x.val()) {
            if (CryptoJS.AES.decrypt(x.val()[k].receipt, "receiptEncrypted").toString(CryptoJS.enc.Utf8) === receipt) {
                order = {
                    user: CryptoJS.AES.decrypt(x.val()[k].user, "emailEncrypted").toString(CryptoJS.enc.Utf8),
                    items: CryptoJS.AES.decrypt(x.val()[k].items, "mycartEncrypted").toString(CryptoJS.enc.Utf8),
                    total: CryptoJS.AES.decrypt(x.val()[k].total, "totalEncrypted").toString(CryptoJS.enc.Utf8),
                    details: CryptoJS.AES.decrypt(x.val()[k].details, "detailsEncrypted").toString(CryptoJS.enc.Utf8),
                    dateordered: CryptoJS.AES.decrypt(x.val()[k].dateordered, "dtEncrypted").toString(CryptoJS.enc.Utf8),
                    confirmed: x.val()[k].confirmed
                }
                return order;
            }
        }
        return 0;
    }

