import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
// import logo from './img/logo.png';

const Navbartop = () => {
    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            document.getElementsByClassName("Navbartop")[0].style.display = "block";
        } else {
            document.getElementsByClassName("Navbartop")[0].style.display = "none";
            history.push("/");
        }
    });

    function logout() {
        console.log('logout');
        sessionStorage.clear();
        document.getElementsByClassName("Navbartop")[0].style.display = "none";
        document.getElementsByClassName("Footerbottom")[0].style.display = "none";
        history.push("/");
    }
    return (
        <div className="Navbartop">
            <Navbar bg="dark" expand="lg" variant="dark">
                {/*<Navbar.Brand href="#home">Care</Navbar.Brand>*/}
                {/* <img src={logo} alt="logo" /> */}
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link href="#home">Home</Nav.Link>
                        <Nav.Link href="#custom">Donate</Nav.Link>
                        <Nav.Link href="#sponsor">Sponsor</Nav.Link>
                        <Nav.Link href="#my-cart">My Cart</Nav.Link>
                        <Nav.Link href="#">About us</Nav.Link>
                    </Nav>
                    <Form inline>
                        <Button variant="outline-success" onClick={logout}>Logout</Button>
                    </Form>
                </Navbar.Collapse>
            </Navbar>
        </div>      
    );
}

export default Navbartop;
