import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import './App.css';
import sponsor1 from './img/sponsor1.jpg';
import sponsor2 from './img/sponsor2.jpg';
// import firebase from './firebase';

const Sponsor = () => {
    const history = useHistory();

  useEffect(() => {        
    begin();    
  });

  async function begin() {
    console.log("starting up");
    let cards = document.getElementsByClassName("sponsorCard");
    for (let i = 1; i < cards.length; i += 2) {
        document.getElementsByClassName("sponsorCard")[i].classList.add("sponsorCardRight");
    }
  }

  function sponsorFamily(n) {
    history.push("/sponsorFamily?family=" + n);
  }

  return (
    <div className="Sponsor">
        <div className="content">
            <br />
            <h4>Sponsor a family</h4>
            <br />
            <p>With your donations, you will receive updates on the wellbeing of your chosen family.</p>
            <br />
            <div className="sponsorCard">
                <img src={sponsor1} alt="sponsor" />
                <div className="text">
                    <b>The Johnsons</b>
                    <p>The Johnsons are a young family in need of food and other supplies.</p>
                    <button onClick={() => sponsorFamily(1)} className="btn btn-primary">Sponsor the Johnsons for $100 a month</button>
                </div>                
            </div>
            <div className="sponsorCard">
                <img src={sponsor2} alt="sponsor" />
                <div className="text">
                    <b>Jane Doe</b>
                    <p>Jane is a single mother in need of financial assistance to take care of her children.</p>
                    <button onClick={() => sponsorFamily(2)} className="btn btn-primary">Sponsor Jane for $120 a month</button>
                </div>
            </div>
            <div className="sponsorCard">
                <img src={sponsor1} alt="sponsor" />
                <div className="text">
                    <b>The Johnsons</b>
                    <p>The Johnsons are a young family in need of food and other supplies.</p>
                    <button onClick={() => sponsorFamily(3)} className="btn btn-primary">Sponsor the Johnsons for $100 a month</button>
                </div>                
            </div>
            <div className="sponsorCard">
                <img src={sponsor2} alt="sponsor" />
                <div className="text">
                    <b>Jane Doe</b>
                    <p>Jane is a single mother in need of financial assistance to take care of her children.</p>
                    <button onClick={() => sponsorFamily(3)} className="btn btn-primary">Sponsor Jane for $120 a month</button>
                </div>
            </div>
            <div className="sponsorCard">
                <img src={sponsor1} alt="sponsor" />
                <div className="text">
                    <b>The Johnsons</b>
                    <p>The Johnsons are a young family in need of food and other supplies.</p>
                    <button onClick={() => sponsorFamily(3)} className="btn btn-primary">Sponsor the Johnsons for $100 a month</button>
                </div>                
            </div>
            <div className="sponsorCard">
                <img src={sponsor2} alt="sponsor" />
                <div className="text">
                    <b>Jane Doe</b>
                    <p>Jane is a single mother in need of financial assistance to take care of her children.</p>
                    <button onClick={() => sponsorFamily(3)} className="btn btn-primary">Sponsor Jane for $120 a month</button>
                </div>
            </div>
        </div>
    </div>
  );
}

export default Sponsor;
