import React, { useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';


const DonateMoney = () => {
    // const [cardNumber, setCardnumber] = useState("");
    // const [name, setName] = useState("");

    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");        
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            setReference(savedEmail);
        } else {
            history.push("/");
        }
    });

    function setReference(savedEmail) {
        if (document.getElementById("referenceID")) {
            if (savedEmail) {
                document.getElementById("referenceID").innerHTML = savedEmail;
            } else {
                document.getElementById("referenceID").innerHTML = "<i>Your email address</i>";
            }
        } 
    }

    function makeCardDonation() {
        console.log("makeing card donaiton");
    }

    return (
        <div className="DonateMoney">
            <div className="content">
                <br />
                <h3>Donate Money</h3>
                <br />
                <p>Thank you for your generosity. Please fill in the form to make a donation:</p>
                <br />
                <div className="paymentSection">
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                Bank transfer
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td><b>BSB:</b></td>
                                            <td>0123 4567</td>
                                        </tr>
                                        <tr>
                                            <td><b>Account Number:</b></td>
                                            <td>0123 4567 8901</td>
                                        </tr>
                                        <tr>
                                            <td><b>Reference:</b></td>
                                            <td id="referenceID">0123 4567 8901</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="1">
                                Credit Card Payment
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                            <Card.Body>

                                <form onSubmit={makeCardDonation}>
                                    <div className="form-row">
                                        <div className="col-md-12 mb-3">
                                            <label for="validationDefault01">Card Number</label>
                                            <input type="text" className="form-control" id="validationDefault01" placeholder="First name" value="Card Number" required />
                                        </div>
                                    </div>
                                    <div className="form-row">
                                        <div className="col-md-12 mb-3">
                                            <label for="validationDefault01">Name on card</label>
                                            <input type="text" className="form-control" id="validationDefault01" placeholder="First name" value="Name" required />
                                        </div>
                                    </div>
                                    
                                    <div className="form-row">
                                        <div className="col-md-6 mb-3">
                                            <label for="validationDefault01">Expiry (mm/yy)</label>
                                            <input type="text" className="form-control" id="validationDefault01" placeholder="First name" value="mm/yy" required />
                                        </div>
                                        <div className="col-md-6 mb-3">
                                            <label for="validationDefault01">CVV</label>
                                            <input type="text" className="form-control" id="validationDefault01" placeholder="First name" value="CVV" required />
                                        </div>
                                    </div>
                                    <button class="btn btn-primary" type="submit">Submit form</button>
                                </form>

                                
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>             
            </div>
            <br />
        </div>      
    );
}

export default DonateMoney;
