import React from 'react';
import mastercard from './img/mastercard.png';
import visa from './img/visa.png';

const PaymentSection = () => {


    function makeCardDonation() {
        console.log("making card donaiton");
    }

    return (
        <div id="PaymentSection">
            <img src={mastercard} alt="card" />
            <img src={visa} alt="card" />
            <br />
            <br />
            {/* <i class="fa fa-cc-mastercard"></i>
            <i class="fa fa-cc-visa"></i> */}
            <form onSubmit={makeCardDonation}>
                <div className="form-row">
                    <div className="col-md-12 mb-3">
                        <label for="validationDefault01">Card Number</label>
                        <input type="text" className="form-control" id="validationDefault01" placeholder="Card Number" required />
                    </div>
                </div>
                <div className="form-row">
                    <div className="col-md-12 mb-3">
                        <label for="validationDefault01">Name on card</label>
                        <input type="text" className="form-control" id="validationDefault01" placeholder="Name" required />
                    </div>
                </div>
                <div className="form-row">
                    <div className="col-md-6 mb-3">
                        <label for="validationDefault01">Expiry (mm/yy)</label>
                        <input type="text" className="form-control" id="validationDefault01" placeholder="mm/yy" required />
                    </div>
                    <div className="col-md-6 mb-3">
                        <label for="validationDefault01">CVV</label>
                        <input type="text" className="form-control" id="validationDefault01" placeholder="CVV" required />
                    </div>
                </div>
                <div className="form-row">
                    <div className="col-md-12 mb-3">
                        <label for="validationDefault01">Reference</label>
                        <input type="text" className="form-control" id="reference01" placeholder="Reference" />
                    </div>
                </div>
                {/* <button class="btn btn-primary" type="submit">Submit payment</button> */}
            </form>
        </div>
                                    
    );
}

export default PaymentSection;
