        let passEncrypt = CryptoJS.AES.encrypt(pass, "passEncrypted").toString();
        // console.log(CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString());
        console.log(CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString());
        if (CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString() === "61646d696e") {
            // god mode 
            // sees orders, products and links
            document.getElementById("products").style.display = "block";
            return true;
        } else if (CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString() === "6f7264657273") {
            // just sees orders
            return true;
        }
        return false;

