    async function getFood() {
        // sessionStorage.setItem("myCart", null);
        document.getElementsByClassName("spinner")[0].style.display = "block";
        setPageloaded(1);
        let items = await firebase.getFood();        
        let food = [];
        for (let i = 0; i < items.length; i++) {
            if (items[i].category !== "pack") {
                food.push(items[i]);
            }
        }
        let html = "";
        if (food.length > 0) {
            html += "<table class='productList'><tbody>"
            for (let i = 0; i < food.length; i++) {
                if (i % 4 === 0) {
                    if (i === 0) {
                        html += "<tr>"
                    } else {
                        html += "</tr><tr>"
                    }
                }
                html += `
                <td>
                    <div class='card'>                    
                        <h5 class='desc'>` + food[i].desc + ` - $` + food[i].price + `</h5>
                        <img card-img-top src="` + food[i].src + `"></img>
                        <br>
                        <button class='removeItem btn btn-danger btn-sm' value="` + food[i].desc + `">Remove item</button>
                    </div>
                </td>
                `;    
                
            }
            html += "</tr></tbody></table>";
            document.getElementById("foodItems").innerHTML = html;
            document.getElementsByClassName("spinner")[0].style.display = "none";
            await removeItem();            
        }
    }
    
    async function removeItem() {
        let btns = document.getElementsByClassName("removeItem");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                //console.log("item si " + btns[i].value);
                await firebase.removeItem(btns[i].value);
                // show notification
                addNotification(`alert alert-warning`, `Removed item <strong>` + btns[i].value + `</strong> from database. Please refresh the page to see effect.`);
            }
        }
    }

    async function addItem(event) {
        event.preventDefault();
        let food = await firebase.getFood();        
        let proceed = true;
        for (let i = 0; i < food.length; i++) {
            if (food[i].desc === desc) {
                //console.log("that item is already in database");
                addNotification("alert alert-warning", `"` + desc + `" is already in the database. Please remove it if you want to proceed`);
                proceed = false;
            } 
        }
        if (proceed === true) {
            let p = price.replace("$", "");
            let category = document.getElementById("category").value;
            await firebase.addItem(desc, src, p, category.toLowerCase());
            addNotification("alert alert-success", `"` + desc + `" has been added to the database`);
            setDesc("");
            setPrice("");
            setSrc("");
        }
    }

    function addNotification(type, msg) {
        let html = document.getElementById("alertNotification").innerHTML;
        let notificationCount = document.getElementsByClassName("notificationBadge").length;
        html += `
        <div class="notificationBadge">
            <div class="` + type + `" role="alert">
                ` + msg + `
            </div>
        </div>                
        `;
        document.getElementById("alertNotification").innerHTML = html;
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
        // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
        setTimeout(function(){ 
            if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
                document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
            }
        }, 4000);
    }

