import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Spinner from 'react-bootstrap/Spinner';

const Custom = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                loadItems();
            } else {
                history.push("/");
            }
        }        
    });

    async function loadItems() {        
        let url = window.location.href;
        if (url.includes("#medicineID")) {
            document.getElementById("headingID").innerHTML = "Donate Medicinal Equipment";
        } else {
            document.getElementById("headingID").innerHTML = "Donate a Customised Pack";
        }
        document.getElementsByClassName("spinner")[0].style.display = "block";
        let items = await firebase.getFood();
        getItems(items);
    }

    async function getItems(items) {
        setPageloaded(1);

        let food = [];
        for (let i = 0; i < items.length; i++) {
            if (items[i].category !== "pack") {
                let url = window.location.href;
                if (url.includes("#medicineID")) {
                    if (items[i].category === "medicinal") {
                        food.push(items[i]);
                    }
                } else {
                    food.push(items[i]);
                }
            }
        }


        let html = "";
        if (food.length > 0) {
            html += "<hr></hr><table class='productList'><tbody>"
            for (let i = 0; i < food.length; i++) {
                if (i % 4 === 0) {
                    if (i === 0) {
                        html += "<tr>"
                    } else {
                        html += "</tr><tr>"
                    }
                }
                html += `
                <td>
                    <div class='card'>                    
                        <h5 class='desc'>` + food[i].desc + ` - $` + food[i].price + `</h5>
                        <img card-img-top src="` + food[i].src + `"></img>
                        <br>
                        <div class='btnClass'><button class='addToCartBtn btn btn-primary btn-sm' value="` + food[i].id + `">&#43;</button><div class='qty'> ` + getQty(food[i].id) + `</div><button class='removeItemBtn btn btn-secondary btn-sm' value="` + food[i].id + `">-</button></div>
                    </div>
                </td>
                `;
                
            }
            html += "</tr></tbody></table>";
            if (document.getElementById("foodItems")) {
                document.getElementById("foodItems").innerHTML = html;
                document.getElementsByClassName("spinner")[0].style.display = "none";
            }
            await addToCartBtnsActivate(items);
        }
    }

    function getQty(item) {
        let n = 0;
        let myCart = sessionStorage.getItem("myCart");
        if (myCart) {
            myCart = myCart.split(",");
            for (let i = 0; i < myCart.length; i++) {
                if (parseInt(myCart[i]) === parseInt(item)) {
                    n++;
                }
            }
        }        
        return n;
    }
    
    function removeCartItem(item, items) {
        // remove whole entire item w quantity from order
        console.log("item was " + item);
        let myCart = sessionStorage.getItem("myCart");
        myCart = myCart.split(",");
        for (let i = 0; i < myCart.length; i++) {
            if (myCart[i] === item) {
                myCart.splice(i, 1);
                i = myCart.length;
            }
        }
        myCart = myCart.toString();
        sessionStorage.setItem("myCart", myCart);
        getItems(items);
    }

    async function addToCartBtnsActivate(items) {
        let btns = document.getElementsByClassName("addToCartBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                //console.log("item si " + btns[i].value);
                let myCart = sessionStorage.getItem("myCart");
                myCart += "," + btns[i].value;
                // myCart.push(btns[i].value);
                console.log(myCart);
                // sessionStorage.setItem("myCart", myCart.toString());
                sessionStorage.setItem("myCart", myCart);
                getItems(items);
            }
        }

        let btnsRem = document.getElementsByClassName("removeItemBtn");
        for (let i = 0; i < btnsRem.length; i++) {
            btnsRem[i].onclick = async function(){
                removeCartItem(btnsRem[i].value, items);
            }
        }
    }
    
    function donationProceed() {
        history.push("/my-cart");
    }

    return (
        <div className="Custom">
            <div className="content">
                <br />
                <div id="alertNotification"></div>                
                <br />
                <h3 id="headingID">Donate a Customised Pack</h3>
                <br />
                <p>Thank you for your generosity. Please select which items you wish to purchase and donate:</p>
                <p>You can alter this list in <a href="#my-cart">My Cart</a> on the next page</p>
                <br />

                <Spinner animation="border" className="spinner" />
                <div id="foodItems"></div>
                <br />       

   
                <br />
                <hr></hr>
                <div className="confirmBtn">
                    <button type="button" className="btn btn-success" onClick={donationProceed}>Confirm donation items</button>
                </div>
            </div>
            <br />            
            <br />            

        </div>      
    );
}

export default Custom;
