import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";

const ErrorPage = () => {
    const history = useHistory();
    
    function tokenCheck() {
        let url = window.location.href;
        console.log("url:" + url);
        let tkn = "";
        if (url.includes("token=") && url.includes("#")) {
            tkn = url.split("token=")[1].split("#")[0];
            console.log("token is " + tkn);
            sessionStorage.setItem("token", tkn);
        } else {
            console.log("invalid url");
            console.log("mayb check if user has login access?");
        }
    }

    useEffect(() => {
        tokenCheck();
        history.push("/");
    });
    return (
        <div className="ErrorPage">
            
        </div>      
    );
}

export default ErrorPage;
