import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import firebase from './firebase';

const Delivery = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const [name, setName] = useState("");
    const [address, setAddress] = useState("");
    const [phone, setPhone] = useState("");
    const [notes, setNotes] = useState("");

    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                pageLoad();
                getDetails();
            } else {
                history.push("/");
            }
        }        
    });

    function pageLoad() {
        setPageloaded(1);
    }

    function setDetails(event) {
        event.preventDefault();
        let orderDetails = {
            name: name,
            address: address,
            phone: phone,
            notes: notes,
            username: document.getElementById("purchasorUserName").value,
            userphone: document.getElementById("purchasorPhone").value
        };
        sessionStorage.setItem("orderDetails", JSON.stringify(orderDetails));
        confirmation();
    }

    async function getDetails() {
        let savedEmail = sessionStorage.getItem("savedEmail");
        let details = await firebase.getUserDetails(savedEmail);
        document.getElementById("purchasorUserName").value = details.firstName + " " + details.lastName;
        document.getElementById("purchasorPhone").value = details.userphone;
        // setPhone(details.phone);
        console.log("details");
        console.log(details);
    }

    async function confirmation() {
        let myCart = sessionStorage.getItem("myCart");
        let orderDetails = sessionStorage.getItem("orderDetails");
        let obj = JSON.parse(orderDetails);

        // get total
        let myCart2 = myCart.split(",");
        let food = await firebase.getFood();
        let total = 0;
        for (let i = 0; i < myCart2.length; i++) {
            for (let j = 0; j < food.length; j++) {
                if (myCart2[i] === food[j].id.toString()) {
                    total += food[j].price;
                    j = food.length;
                }
            }
        }
        let pass = 1;        
        if (total <= 0) {
            pass = 0;
            addNotification("alert-danger", "Your order is invalid");
        }
        if (obj) {
            if (obj.name === "" || obj.address === "") {
                pass = 0;
                addNotification("alert-danger", "Your recipient cannot be left blank");
            }
        } else {
            pass = 0;
            addNotification("alert-danger", "Your recipient cannot be left blank");
        }
        
        if (pass === 1) {
            sessionStorage.setItem("orderTotal", total);
            history.push("/confirmation");
        }
    }

    function addNotification(type, msg) {
        let html = document.getElementById("alertNotification").innerHTML;
        let notificationCount = document.getElementsByClassName("notificationBadge").length;
        html += `
        <div class="notificationBadge">
            <div class="` + type + `" role="alert">
                ` + msg + `
            </div>
        </div>                
        `;
        document.getElementById("alertNotification").innerHTML = html;
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
        // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
        setTimeout(function(){ 
            if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
                document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
            }
        }, 4000);
    }

    return (
        <div className="Delivery">
            <div className="content">
            <br />
                <div id="alertNotification"></div>                
                <br />
                <h3>Delivery details</h3>
                <br />
                <hr></hr>
                <br />
                <h4>My details</h4>
                <p>Please confirm these are your details</p>
                <form>
                    <div className="form-group">
                        <label>Your name</label>
                        <input id="purchasorUserName" required className="form-control" type="text"></input>
                        <br />
                        <label>Your phone number</label>
                        <input id="purchasorPhone" required className="form-control" type="text"></input>
                    </div>
                </form>

                <br />
                <hr></hr>
                <br />
                <h4>Recipient details</h4>
                <p>Please enter the name and address you wish these items to be donated to</p>
                <div className="recipient">
                    <Form onSubmit={setDetails} autoComplete="off">
                        <Form.Group controlId="formName">
                            <Form.Label>Name of recipient (the person or people receiving the item)</Form.Label>
                            <Form.Control required value={name} onChange={(e) => setName(e.target.value)} type="text" placeholder="Name" />
                        </Form.Group>
                        <Form.Group controlId="formAddress">
                            <Form.Label>Address</Form.Label>
                            <Form.Control required value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Address" />
                        </Form.Group>
                        <Form.Group controlId="formPhone">
                            <Form.Label>Phone</Form.Label>
                            <Form.Control required value={phone} onChange={(e) => setPhone(e.target.value)} type="text" placeholder="Phone" />
                        </Form.Group>
                        <Form.Group controlId="formTextArea">
                            <Form.Label>Other Notes</Form.Label>
                            <Form.Control as="textarea" value={notes} onChange={(e) => setNotes(e.target.value)} type="textarea" placeholder="Other Notes" />
                        </Form.Group>
                        <Button variant="success" type="submit">
                            Confirm
                        </Button>
                    </Form>
                </div>
                <br />

            </div>
            <br />            
            <br />            

        </div>      
    );
}

export default Delivery;
