import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';

const PrintReceipt = () => {
    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            document.getElementsByClassName("Navbartop")[0].style.display = "none";
            document.getElementsByClassName("Footerbottom")[0].style.display = "none";
            getReceipt(savedEmail);
        } else {
            history.push("/");
        }
    });

    async function getReceipt(savedEmail) {
        let url = window.location.href;
        if (url.includes("receipt=") && url.includes("#")) {
            let data = url.split("receipt=")[1].split("#")[0];
            data = data.replaceAll("%22", '"').replaceAll("%20", " ");
            let receiptNum = data.split("&")[0];
            
            let back = window.location.href;
            let html = "<br><a href='" + back.split("printReceipt")[0] + "payment?receipt=" + receiptNum + "'>Go back</a>";
            html += "<br><h3>Receipt Number: " + receiptNum + "</h3><br><br>";
            let myList = [];
            let myCart = data.split("&")[1];
            let myItems = myCart.split(",");            
            html += `<div class="boxDiv"><h4>Your Items</h4><br /><table class="table"><tbody><tr><th scope="col">Item</th><th scope="col">Qty</th><th scope="col">Total</th></tr>`;
            let store = await firebase.getStoreItems();
            let total = 0;
            for (let i = 0; i < myItems.length; i++) {
                for (let j = 0; j < store.length; j++) {
                    if (myItems[i] === store[j].id.toString()) {
                        let itemDesc = store[j].items;
                        if (store[j].category === "pack") {
                            let foodItemsAll = itemDesc.split(",");
                            let foodItems = "<b>Food pack</b>";
                            for (let f = 0; f < foodItemsAll.length; f++) {
                                foodItems += "<li>" + foodItemsAll[f] + "</li>";
                            }
                            itemDesc = foodItems;
                        }
                        let newItem = true;
                        for (let k = 0; k < myList.length; k++) {
                            if (myList[k].desc === itemDesc) {
                                let item = {
                                    desc: myList[k].desc,
                                    qty: myList[k].qty + 1,
                                    total: parseFloat(myList[k].total) + parseFloat(store[j].price)
                                }
                                myList[k] = item;
                                k = myList.length;
                                newItem = false;
                            }
                        }
                        if (newItem === true) {
                            let item = {
                                desc: itemDesc,
                                qty: 1,
                                total: parseFloat(store[j].price)
                            }
                            myList.push(item);
                        }
                        j = store.length;
                    }
                }
            }

            for (let i = 0; i < myList.length; i++) {
                html += `<tr><td>` + myList[i].desc + `</td><td>` + myList[i].qty + `</td><td>$` + myList[i].total.toFixed(2) + `</td></tr>`;
                total += parseFloat(myList[i].total);
            }
            html += `<tr><td><b>Total</b></td><td></td><td><b>$` + total.toFixed(2) + `</b></td></tr>`;
            html += `</tbody></table></div>`;

            let orderDetails = data.split("&")[2];
            let obj = JSON.parse(orderDetails);
            html += `
            <br><br>
            <div class="boxDiv">
                <h4>Delivery Details</h4>
                <table><tbody>
                <tr><th>Name:</th><td>` + obj.name + `</td></tr>
                <tr><th>Address:</th><td>` + obj.address + `</td></tr>
                <tr><th>Purchaser phone:</th><td>` + obj.phone + `</td></tr>
                <tr><th>Notes:</th><td>` + obj.notes + `</td></tr>
                </tbody></table>
            </div>
            `;
            document.getElementById("receiptID").innerHTML = html;
            window.print();
        }
    }

    return (
        <div className="PrintReceipt">
            <div id="receiptID"></div>
        </div>      
    );
}

export default PrintReceipt;
