import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Spinner from 'react-bootstrap/Spinner';

const MyCart = () => {
    const [pageLoaded, setPageloaded] = useState(0);

    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");        
            let myCart = sessionStorage.getItem("myCart");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                getItems(myCart);
            } else {
                history.push("/");
            }
        }        
    });

    async function getItems(myCart) {
        console.log("over here");
        document.getElementsByClassName("spinner")[0].style.display = "block";
        setPageloaded(1);
        let myList = [];
        let html = "";
        if (myCart) {    
            let myItems = myCart.split(",");            
            if (!(myItems.length === 1 && myItems[0] === "null")) {
                html = `<table class="table"><tbody><tr><th scope="col">Item</th><th scope="col">Qty</th><th scope="col">Total</th><th></th></tr>`;
                let store = await firebase.getStoreItems();
                let total = 0;
                for (let i = 0; i < myItems.length; i++) {
                    for (let j = 0; j < store.length; j++) {
                        if (myItems[i] === store[j].id.toString()) {
                            let itemDesc = store[j].items;
                            if (store[j].category === "pack") {
                                let foodItemsAll = itemDesc.split(",");
                                let foodItems = "<b>Food pack</b>";
                                for (let f = 0; f < foodItemsAll.length; f++) {
                                    foodItems += "<li>" + foodItemsAll[f] + "</li>";
                                }
                                itemDesc = foodItems;
                            }
                            let newItem = true;
                            for (let k = 0; k < myList.length; k++) {
                                if (myList[k].desc === itemDesc) {
                                    let item = {
                                        desc: itemDesc,
                                        qty: myList[k].qty + 1,
                                        total: parseFloat(myList[k].total) + parseFloat(store[j].price),
                                        id: store[j].id,
                                        src: store[j].src
                                    }
                                    myList[k] = item;
                                    k = myList.length;
                                    newItem = false;
                                }
                            }
                            if (newItem === true) {
                                let item = {
                                    desc: itemDesc,
                                    qty: 1,
                                    total: parseFloat(store[j].price),
                                    id: store[j].id,
                                    src: store[j].src
                                }
                                myList.push(item);
                            }
                            j = store.length;
                        }
                    }
                }
                let removeText = "Remove item";
                if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                    removeText = "X";
                }
                for (let i = 0; i < myList.length; i++) {
                    // html += `<tr><td>` + myList[i].desc + `</td><td><button type="button" class="addToOrder btn btn-light" value="` + myList[i].id + `">+</button>` + myList[i].qty + `<button type="button" class="removeFromOrder btn btn-light" value="` + myList[i].id + `">-</button></td><td>$` + myList[i].total + `</td><td><button class='removeItemBtn btn btn-danger btn-sm' value="` + myList[i].id + `">` + removeText + `</button></td></tr>`;
                    html += `<tr><td><img src=` + myList[i].src + ` />` + myList[i].desc + `</td><td>` + myList[i].qty + `</td><td>$` + myList[i].total.toFixed(2) + `</td><td><button class='removeItemBtn btn btn-danger btn-sm' value="` + myList[i].id + `">` + removeText + `</button></td></tr>`;
                    total += parseFloat(myList[i].total);
                }
                html += `<tr><td><b>Total</b></td><td></td><td><b>$` + total.toFixed(2) + `</b></td><td></td></tr>`;
                html += `</tbody></table>`;
            } else {
                html = "<p>You haven't selected any items to be donated. Please <a href='#custom'>Click here</a> to select items to donate</p>";
            }        
        } else {
            html = "<p>You haven't selected any items to be donated. Please <a href='#custom'>Click here</a> to select items to donate</p>";
        } 
        if (document.getElementById("myItems")) {
            document.getElementById("myItems").innerHTML = html;
            console.log("myCart:" + myCart);
            document.getElementsByClassName("spinner")[0].style.display = "none";
        }
        
        await setBtns();
    }

    async function setBtns() {
        let btns = document.getElementsByClassName("removeItemBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                removeCartItem(btns[i].value);
            }
        }

        // let btnsAdd = document.getElementsByClassName("addToOrder");
        // for (let i = 0; i < btnsAdd.length; i++) {
        //     btnsAdd[i].onclick = async function(){
        //         addToCart(btnsAdd[i].value);
        //     }
        // }

        // let btnsRem = document.getElementsByClassName("removeFromOrder");
        // for (let i = 0; i < btnsRem.length; i++) {
        //     btnsRem[i].onclick = async function(){
        //         removeFromCart(btnsRem[i].value);
        //     }
        // }
    }

    // function removeFromCart(item) {
    //     // remove single item quantity from order
    //     let myCart = sessionStorage.getItem("myCart");
    //     myCart = myCart.split(",");
    //     for (let j = 0; j < myCart.length; j++) {
    //         if (myCart[j] === item) {
    //             myCart.splice(j, 1);
    //             j = myCart.length;
    //         }
    //     }
    //     myCart = myCart.toString();
    //     sessionStorage.setItem("myCart", myCart);
    //     getItems(myCart);
    // }

    // function addToCart(item) {
    //     let myCart = sessionStorage.getItem("myCart");
    //     myCart += "," + item;
    //     sessionStorage.setItem("myCart", myCart);
    //     getItems(myCart);
    // }

    function removeCartItem(item) {
        // remove whole entire item w quantity from order
        console.log("item was " + item);
        let myCart = sessionStorage.getItem("myCart");
        myCart = myCart.split(",");
        for (let i = 0; i < myCart.length; i++) {
            if (myCart[i] === item) {
                myCart.splice(i, 1);
                i = 0;
            }
        }
        myCart = myCart.toString();
        sessionStorage.setItem("myCart", myCart);
        getItems(myCart);
    }

    function donateMore() {
        history.push("/custom");
    }

    function delivery() {
        history.push("/delivery");
    }

    return (
        <div className="MyCart">
            <div className="content">
                <br />
                <h3>My Cart</h3>
                <br />
                <Spinner animation="border" className="spinner" />
                <div id="myItems"></div>
                <br />
                <br />
                <div className="buttonSpace">
                    <button type="button" className="btn btn-primary left" onClick={donateMore}>Edit order</button>
                    <button type="button" className="btn btn-success right" onClick={delivery}>Add delivery details</button>
                </div>
                <br />       

                
                {/*}
                <hr></hr>
                <br />
                <button type="button" className="btn btn-success" onClick={confirmation}>Proceed with payment options</button>
                */}

                
            </div> 
        </div>      
    );
}

export default MyCart;
