import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Spinner from 'react-bootstrap/Spinner';

const DonateFood = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                getFood();
            } else {
                history.push("/");
            }
        }        
    });

    async function getFood() {

        // sessionStorage.setItem("myCart", null);
        document.getElementsByClassName("spinner")[0].style.display = "block";
        setPageloaded(1);
        let food = await firebase.getFoodPacks();        
        let html = "";
        let foodPackColours = ["purple", "green", "orange"];
        if (food.length > 0) {
            html += "<table class='foodPackTable'><tbody>"
            for (let i = 0; i < food.length; i++) {
                if (i % 4 === 0) {
                    if (i === 0) {
                        html += "<tr>"
                    } else {
                        html += "</tr><tr>"
                    }
                }
                let foodItemsAll = food[i].items.replace("[", "").replace("]", "").split(",");
                let foodItems = "";
                for (let f = 0; f < foodItemsAll.length; f++) {
                    foodItems += "<li>" + foodItemsAll[f] + "</li>";
                }
                html += `
                <td>
                    <div class='card ` + foodPackColours[i % foodPackColours.length] + `Card '>                    
                        <img class="card-img-top" src="` + food[i].src + `"></img>
                        <div class='card-body'>
                            <h5 class='card-title desc'>Food Pack ` + (i+1) + ` - $` + food[i].price + `</h5>
                            <p class="card-text">` + foodItems + `</p>
                            <button class='addToCartBtn btn btn-primary btn-sm' value="` + food[i].id + `">Donate item</button>
                        </div>
                    </div>
                </td>
                `;
                
            }
            html += "</tr></tbody></table>";
            document.getElementById("foodItems").innerHTML = html;
            document.getElementsByClassName("spinner")[0].style.display = "none";
            await addToCartBtnsActivate();
        }
    }

    async function addToCartBtnsActivate() {
        let btns = document.getElementsByClassName("addToCartBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                //console.log("item si " + btns[i].value);
                let myCart = sessionStorage.getItem("myCart");
                myCart += "," + btns[i].value;
                // myCart.push(btns[i].value);
                console.log(myCart);
                // sessionStorage.setItem("myCart", myCart.toString());
                sessionStorage.setItem("myCart", myCart);

                // show notification
                addNotification(`alert alert-success`, `Added item to donation cart`);
                
            }
        }
    }

    function addNotification(type, msg) {
        let html = document.getElementById("alertNotification").innerHTML;
        let notificationCount = document.getElementsByClassName("notificationBadge").length;
        html += `
        <div class="notificationBadge">
            <div class="` + type + `" role="alert">
                ` + msg + `
            </div>
        </div>                
        `;
        document.getElementById("alertNotification").innerHTML = html;
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
        // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
        setTimeout(function(){ 
            if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
                document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
            }
        }, 4000);
    }

    function donationProceed() {
        history.push("/my-cart");
    }

    // async function addPack() {
    //     // await firebase.addFoodPack();
    //     await firebase.makeIndex();
    //     document.getElementById("packID").disabled = true;
    // }

    return (
        <div className="DonateFood">
            <div className="content">
                <br />
                <div id="alertNotification"></div>                
                <br />
                <h3>Donate Food Packs</h3>
                <br />
                <p>Thank you for your generosity. Please select which items you wish to purchase and donate:</p>
                <p>Or you can customise your own food pack <a href="#custom">here</a></p>
                <br />

                <hr></hr>
                <Spinner animation="border" className="spinner" />
                <div id="foodItems"></div>
                <br />       

   
                <br />
                <hr></hr>

                <button type="button" className="btn btn-success" onClick={donationProceed}>Confirm donation items</button>

                {/*<button id="packID" type="button" className="btn btn-primary" onClick={addPack}>Add packs</button>*/}
            </div>
            <br />            
            <br />            

        </div>      
    );
}

export default DonateFood;
