import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Spinner from 'react-bootstrap/Spinner';
import firebase from './firebase';
import PaymentSection from './paymentSection';
import paypal from './img/paypal.png';

const Payment = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    // const [cardNumber, setCardnumber] = useState("");
    // const [name, setName] = useState("");
    const history = useHistory();
    
    useEffect(() => {
        document.getElementsByClassName("paymentSection")[0].style.display = "none";

        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                document.getElementsByClassName("Navbartop")[0].style.display = "block";
                document.getElementsByClassName("Footerbottom")[0].style.display = "block";
                confirmPayment();
            } else {
                history.push("/");
            }
        }        
    });

    async function confirmPayment() {
        // sessionStorage.setItem("myCart", null);
        setPageloaded(1);

        // get receipt confirmation
        let html = "";
        let myList = [];
        let myCart = sessionStorage.getItem("myCart");
        if (myCart) {    
            let myItems = myCart.split(",");            
            if (!(myItems.length === 1 && myItems[0] === "null")) {
                html = `<div class="boxDiv"><h4>Your Items</h4><br /><table class="table"><tbody><tr><th scope="col">Item</th><th scope="col">Qty</th><th scope="col">Total</th></tr>`;
                console.log(myItems);
                let store = await firebase.getStoreItems();
                let total = 0;
                for (let i = 0; i < myItems.length; i++) {
                    for (let j = 0; j < store.length; j++) {
                        if (myItems[i] === store[j].id.toString()) {
                            let itemDesc = store[j].items;
                            if (store[j].category === "pack") {
                                let foodItemsAll = itemDesc.split(",");
                                let foodItems = "<b>Food pack</b>";
                                for (let f = 0; f < foodItemsAll.length; f++) {
                                    foodItems += "<li>" + foodItemsAll[f] + "</li>";
                                }
                                itemDesc = foodItems;
                            }
                            let newItem = true;
                            for (let k = 0; k < myList.length; k++) {
                                if (myList[k].desc === itemDesc) {
                                    let item = {
                                        desc: myList[k].desc,
                                        qty: myList[k].qty + 1,
                                        total: parseFloat(myList[k].total) + parseFloat(store[j].price)
                                    }
                                    myList[k] = item;
                                    k = myList.length;
                                    newItem = false;
                                }
                            }
                            if (newItem === true) {
                                let item = {
                                    desc: itemDesc,
                                    qty: 1,
                                    total: parseFloat(store[j].price)
                                }
                                myList.push(item);
                            }
                            j = store.length;
                        }
                    }
                }

                for (let i = 0; i < myList.length; i++) {
                    html += `<tr><td>` + myList[i].desc + `</td><td>` + myList[i].qty + `</td><td>$` + myList[i].total.toFixed(2) + `</td></tr>`;
                    total += parseFloat(myList[i].total);
                }
                html += `<tr><td><b>Total</b></td><td></td><td><b>$` + total.toFixed(2) + `</b></td></tr>`;
                html += `</tbody></table></div>`;
                
                // get receipt number 
                let url = window.location.href;
                if (url.includes("receipt=") && url.includes("#")) {
                    let receiptNum = url.split("receipt=")[1].split("#");
                    document.getElementById("receiptID").innerHTML = "Your receipt number is <b>" + receiptNum + "</b>. Please include this in your reference number during payment.";
                    document.getElementById("receiptID").innerHTML += "<br><b>Please keep this receipt number handy to keep track of your order</b>";
                    document.getElementById("receiptID").innerHTML += "<br>Click <a href='" + url.split("payment")[0] + "printReceipt?receipt=" + receiptNum + "&" + myCart + "&" + sessionStorage.getItem("orderDetails") + "'>here</a> for a printer friendly version of the receipt";
                    document.getElementById("referenceID").innerHTML = receiptNum;

                    if (document.getElementById("paymentCredit").style.display !== "none") {
                        document.getElementById("reference01").value = receiptNum;
                        document.getElementById("reference01").readOnly = true;
                    }
                }

                // get delivery details
                let orderDetails = sessionStorage.getItem("orderDetails");
                let obj = JSON.parse(orderDetails);
                document.getElementById("deliveryDetails").innerHTML = `
                <div class="boxDiv">
                    <h4>Delivery Details</h4>
                    <table><tbody>
                    <tr><th>Name:</th><td>` + obj.name + `</td></tr>
                    <tr><th>Address:</th><td>` + obj.address + `</td></tr>
                    <tr><th>Purchaser phone:</th><td>` + obj.phone + `</td></tr>
                    <tr><th>Notes:</th><td>` + obj.notes + `</td></tr>
                    </tbody></table>
                </div>
                `;
            } else {
                html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
            }        
        } else {
            html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
        } 
        document.getElementsByClassName("spinner")[0].style.display = "none";
        document.getElementsByClassName("paymentSection")[0].style.display = "block";
        
        document.getElementById("myItems").innerHTML = html;
    }

    function showPayment(op) {
        document.getElementById("paymentCredit").style.display = "none";
        document.getElementById("paymentBank").style.display = "none";
        document.getElementById("paymentPaypal").style.display = "none";
        document.getElementById(op).style.display = "block";        
    }

    return (
        <div className="Payment">
            <div className="content">
                <br />
                <div id="alertNotification"></div>                
                <br />
                <h3>Confirm donation</h3>
                <br />
                <p>Thank you for your generosity. Please enter the payment details below:</p>
                <br />
                <h4>Your receipt:</h4>
                <div id="receiptID"></div>
                <br />                
                <br />
                <Spinner animation="border" className="spinner" />
                <h4>Payment section</h4>
                <br />
                <div className="paymentSection">
                    <table>
                        <tbody>
                            <tr>
                                <td className="paymentTable" rowSpan="2">
                                    <div id="paymentProcess" className="boxDiv">
                                        <h4>Please select the method of payment</h4>
                                        <br />
                                        <table><tbody><tr>
                                            <td><button className="btn btn-info" onClick={()=>showPayment("paymentCredit")}>Credit Card</button></td>
                                            <td><button className="btn btn-info" onClick={()=>showPayment("paymentBank")}>Bank Transfer</button></td>
                                            <td><button className="btn btn-info" onClick={()=>showPayment("paymentPaypal")}>Paypal</button></td>
                                        </tr></tbody></table>
                                        <br />
                                        <div id="paymentCredit">
                                            <PaymentSection />
                                            <button class="btn btn-primary" type="submit">Submit payment</button>
                                        </div>                                        
                                        <div id="paymentBank" style={{display:"none"}}>
                                            <table>
                                                <tbody>
                                                    <tr>
                                                        <td><b>BSB:</b></td>
                                                        <td>0123 4567</td>
                                                    </tr>
                                                    <tr>
                                                        <td><b>Account Number:</b></td>
                                                        <td>0123 4567 8901</td>
                                                    </tr>
                                                    <tr>
                                                        <td><b>Reference:</b></td>
                                                        <td id="referenceID">0123 4567 8901</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                        <div id="paymentPaypal" style={{display:"none"}}>
                                            {/* <i class="fa fa-paypal"></i> */}
                                            <img src={paypal} alt="card" />
                                            <br />
                                            <br />
                                            <button class="btn btn-primary">Sign in via paypal</button>
                                        </div>
                                    </div>
                                </td>
                                <td className="paymentTable">
                                    <div id="myItems"></div>

                                </td>
                            </tr>
                            <tr>
                                <td className="paymentTable">
                                    <div id="deliveryDetails"></div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <br />                
                </div>      
            </div>
            <br />            
            <br />            

        </div>      
    );
}

export default Payment;
